(function() {
    'use strict';

    angular
        .module('phoenix')
        .controller('teacherCourseManagementCtrl', teacherCourseManagementCtrl);

    teacherCourseManagementCtrl.$inject = ['$scope', '$uibModal', 'teacherCourseSrv', '$stateParams', '$state'];

    function teacherCourseManagementCtrl($scope, $uibModal, teacherCourseSrv, $stateParams, $state) {
        $scope.checkEx = function(item) {
            $state.go('index.startExperiment.cloudware', { experimentId: item.id, studentId: localStorage['userId'], cloudwareType: item.cloudwareType })
        }
        $scope.classId = $stateParams.classId
        teacherCourseSrv.getCourseDetail().get({
                classId: $stateParams.classId
            },
            function(response) {
                console.log(response)
                if (response.errorCode == 0) {
                    $scope.courseId = response.data.courseId;
                    $scope.courseName = response.data.courseName;
                    $scope.courseContent = response.data.moduleList;
                    for (var i in $scope.courseContent) {
                        $scope.condition.push({
                            label: $scope.courseContent[i].moduleName,
                            value: $scope.courseContent[i].moduleName
                        })
                        $scope.q = $scope.condition[0].value
                    }
                } else {
                    toastr.error(response.message);
                }

            },
            function(error) {
                console.log(error);
                toastr.error("");

            })
        $scope.courseContent = []
        $scope.condition = [{
            label: "",
            value: ""
        }]





    }


})();
