(function() {
    'use strict';

    angular
        .module('phoenix')
        .controller('testCtrl', testCtrl);

    testCtrl.$inject = ['$scope', '$timeout'];

    function testCtrl($scope, $timeout) {
        $('.summernote').summernote({
            height: 600,
            focus: true,

            toolbar: [
                ['edit', ['undo', 'redo']],
                ['headline', ['style']],
                ['style', ['bold', 'italic', 'underline', 'superscript', 'subscript', 'strikethrough', 'clear']],
                ['fontface', ['fontname']],
                ['textsize', ['fontsize']],
                ['fontclr', ['color']],
                ['alignment', ['ul', 'ol', 'paragraph', 'lineheight']],
                ['height', ['height']],
                ['table', ['table']],
                ['insert', ['link', 'picture', 'video', 'hr']],
                ['view', ['fullscreen', 'codeview']],
                ['help', ['help']]
            ]
        });
        $scope.show3 = false;
        $scope.show1 = true;
        $scope.show2 = false;

        $scope.show = function(value) {
            switch (value) {
                case 1:
                    $scope.show1 = true;
                    $scope.show2 = false;
                    $scope.show3 = false

                    var testEditor = editormd("test-editormd", {
                        path: 'markdownLib/',
                        height: 600,
                        onchange: function() {
                            console.log(this.getValue());
                        },
                        imageUpload: true,
                        imageFormats: ["jpg", "jpeg", "gif", "png", "bmp", "webp"],
                        imageUploadURL: 'http://www.x-lab.ac:13001/admin/course/experiment/piclib',
                    });
                    break;
                case 2:
                    $scope.show2 = true;

                    $scope.show1 = false;
                    $scope.show3 = false;
                    break;
                case 3:
                    $scope.show3 = true;
                    $scope.show1 = false;
                    $scope.show2 = false;

                    break;

                default:
                    break;
            }
        }
        $scope.test = function() {
            console.log($scope.text)
        }
        $scope.show(1);
        var converter = new showdown.Converter();
        converter.setOption('tasklists', true);
        converter.setOption('tables', true);
        $.get("app/module/teacher/teacherCourse/test.md", function(result) {
            // console.log(result)
            if (result == null) {
                return
            }
            $scope.text = result;
            console.log($scope.text);
            $scope.html = converter.makeHtml($scope.text);
            $timeout(function() {
                console.log($('#ht').height())
                $('#or').height($('#ht').height());
            })

            // console.log(result.label_type)

        });
        $scope.change = function() {
            console.log($scope.text);
            $scope.html = converter.makeHtml($scope.text);

        }
        $("textarea").on(
            'keydown',
            function(e) {
                $scope.change();
                if (e.keyCode == 9) {
                    e.preventDefault();
                    var indent = '    ';
                    var start = this.selectionStart;
                    var end = this.selectionEnd;
                    var selected = window.getSelection().toString();
                    selected = indent + selected.replace(/\n/g, '\n' + indent);
                    this.value = this.value.substring(0, start) + selected +
                        this.value.substring(end);
                    this.setSelectionRange(start + indent.length, start +
                        selected.length);
                }
            })



    }
})();
(function() {
    'use strict';

    angular
        .module('phoenix')
        .controller('studentHomeworkCtrl', studentHomeworkCtrl);

    studentHomeworkCtrl.$inject = ['$scope', '$state', 'stuCourseSrv', '$stateParams'];

    function studentHomeworkCtrl($scope, $state, stuCourseSrv, $stateParams) {
        $scope.doHomework = function(item) {
            $state.go('index.studentDoHomework.cloudware', { homeworkId: item.id, studentId: localStorage['userId'], cloudwareType: item.cloudwareType });
        }
        stuCourseSrv.getAllHomework().get({
                classId: $stateParams.classId,
                studentId: localStorage['userId']
            }, function(response) {
                console.log(response)
                if (response.errorCode == 0) {

                    $scope.courseId = response.data.courseId;
                    $scope.courseName = response.data.courseName;
                    $scope.courseContent = response.data.moduleList;
                    for (var i in $scope.courseContent) {
                        $scope.condition.push({
                            label: $scope.courseContent[i].moduleName,
                            value: $scope.courseContent[i].moduleName
                        })
                    }

                    $scope.q = $scope.condition[0].value
                } else {
                    toastr.error(response.message);
                }
            },
            function(error) {
                toastr.error("");

            })

        $scope.condition = [{
            label: "",
            value: ""
        }]
        $scope.checkGrade = function(item) {
            stuCourseSrv.getHomeworkScore().get({
                homeworkId: item.id,
                studentId: localStorage['userId']
            }).$promise.then(function(response) {
                    console.log(response)
                    if (response.errorCode == 0) {
                        var score;
                        if (response.data.score) {
                            score = response.data.score
                        } else {
                            score = ""
                        }
                        swal({
                            title: score,
                            text: response.data.comment,
                            type: "success",
                            showCancelButton: true,
                            // confirmButtonColor: "#DD6B55",
                            confirmButtonText: "",
                            closeOnConfirm: true,
                            cancelButtonText: "",
                            closeOnCancel: true
                        })
                    }
                },
                function(error) { console.log(error) })

        }

    }
})();
(function() {
    'use strict';

    angular
        .module('phoenix')
        .controller('studentDoHomeworkCtrl', studentDoHomeworkCtrl)
        .controller('uploadReportModalCtrl', uploadReportModalCtrl);

    studentDoHomeworkCtrl.$inject = ['$scope', '$uibModal', '$timeout', 'stuCourseSrv', '$stateParams'];
    uploadReportModalCtrl.$inject = ['$scope', '$uibModalInstance', '$timeout', 'reqUrl', 'stuCourseSrv', '$stateParams', '$state']

    function uploadReportModalCtrl($scope, $uibModalInstance, $timeout, reqUrl, stuCourseSrv, $stateParams, $state) {
        $scope.cancel = function() {
            $uibModalInstance.dismiss('cancel');
        }
        $scope.ok = function() {
            stuCourseSrv.submitHomework().save({
                "homeworkId": $stateParams.homeworkId,
                "homework_url": $scope.homeworkUrl,
                "studentId": $stateParams.studentId
            }).$promise.then(
                function(response) {
                    console.log(response)
                    if (response.errorCode == 0) {
                        toastr.success("")
                        $uibModalInstance.close();

                    } else {

                        toastr.error(response.message)
                    }
                },
                function(error) {
                    console.log(error)
                }
            )
        }
        $scope.chooseFile = function() {
            $('#report').trigger('click');
        }
        $scope.filename = "";
        $scope.submitted = false;

        $scope.getFile = function(file) {
            console.log(file);
            $scope.homeworkFile = new FormData();
            $scope.homeworkFile.append('file', file)
            $scope.filename = file.name;
            $scope.showBtn = true;
            $scope.$apply()
        };
        $scope.upload = function() {
            $.ajax({
                type: 'POST',
                url: reqUrl + '/admin/course/experiment/report',
                dataType: 'json',
                beforeSend: function(xhr) {
                    // xhr.setRequestHeader('access_token', '1504751421487');
                    xhr.setRequestHeader('Authorization', 'Bearer ' + (localStorage['token'] ? localStorage['token'] : ''));
                },
                processData: false, // Dont process the files
                contentType: false,
                data: $scope.homeworkFile,
                success: function(res) {
                    console.log(res)
                    if (res.errorCode == 0) {
                        toastr.success('');
                        $scope.homeworkUrl = res.data;
                        $scope.submitted = true;
                        $scope.$apply();
                    } else if (res.errorCode == 45) {
                        toastr.warning("");
                        localStorage['requireLogin'] = true
                        $state.go("index.main", null, { reload: true })
                    } else if (res.errorCode == 46) {
                        toastr.warning("");
                        localStorage['requireLogin'] = true
                        $state.go("index.main", null, { reload: true })
                    } else {
                        toastr.error(res.message);
                    }
                }
            });
        }

        // $timeout(function() {
        //     // $('<input type="file" id="report" placeholder="" style="display:none"  class="form-control">').appendTo($('#choosefilebtn'));

        // })
    }


    function studentDoHomeworkCtrl($scope, $uibModal, $timeout, stuCourseSrv, $stateParams) {
        var vm = this;
        $scope.commitWork = function() {
            toastr.success("")
        }
        console.log($stateParams);
        stuCourseSrv.getHomeworkDetail().get({
            homeworkId: $stateParams.homeworkId
        }, function(response) {
            console.log(response);
            $scope.detail = response.data
            console.log($scope.detail)
        }, function(error) {
            console.log(error);
        })
        $scope.uploadReport = function() {
            var modalInstance = $uibModal.open({
                templateUrl: 'app/module/modal/uploadReportModal.html',
                controller: 'uploadReportModalCtrl'
            });


            modalInstance.rendered.then(function(result) {
                console.log(result);

            });
            modalInstance.result.then(function(result) {
                console.log(result);

            });
        }

    }
})();
(function() {
    'use strict';

    angular
        .module('phoenix')
        .controller('studentCourseDetailCtrl', studentCourseDetailCtrl);

    studentCourseDetailCtrl.$inject = ['$scope', '$state', 'stuCourseSrv', '$stateParams'];

    function studentCourseDetailCtrl($scope, $state, stuCourseSrv, $stateParams) {
        $scope.checkCourseHomework = function() {
            $state.go('index.studentHomework', { classId: $stateParams.classId })
        }
        $scope.startExp = function(item) {
            if (item.cloudwareType === 'jupyter_python') {
                $state.go('index.startExperiment.notebook', { experimentId: item.id, studentId: localStorage['userId'], cloudwareType: item.cloudwareType });
            } else if (item.cloudwareType === 'ide_java') {
                $state.go('index.startExperiment.webide', { experimentId: item.id, studentId: localStorage['userId'], cloudwareType: item.cloudwareType });
            } else {
                $state.go('index.startExperiment.cloudware', {
                    experimentId: item.id,
                    studentId: localStorage['userId'],
                    cloudwareType: item.cloudwareType
                });
            }
        }
        $scope.doHomework = function() {
            $state.go('index.studentDoHomework');
        }
        stuCourseSrv.getCourseDetail().get({
                classId: $stateParams.classId
            },
            function(response) {
                console.log(response)
                if (response.errorCode == 0) {
                    $scope.courseId = response.data.courseId;
                    $scope.courseName = response.data.courseName;
                    $scope.courseContent = response.data.moduleList;
                    for (var i in $scope.courseContent) {
                        $scope.condition.push({
                            label: $scope.courseContent[i].moduleName,
                            value: $scope.courseContent[i].moduleName
                        })
                        $scope.q = $scope.condition[0].value
                    }
                } else {
                    toastr.error(response.message);
                }

            },
            function(error) {
                console.log(error);
                toastr.error("");

            })
        $scope.courseContent = []
        $scope.condition = [{
            label: "",
            value: ""
        }]




    }
})();
